.footer {
  margin-top: -25px;
  padding-top: 33px;
  background-color: $color-white;
  border-radius: 30px 30px 0 0;
  z-index: 0;

  &--index {
    margin-top: -112px;
  }

  @media (min-width: $tablet-width) {
    margin-top: -48px;
    padding-top: 55px;
    border-radius: 60px 60px 0 0;

    &--index {
      margin-top: -72px;
    }
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 30px;
    margin-top: -53px;

    &--index {
      margin-top: -135px;
    }
  }
}

.footer__container {
  @include container;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}


.footer__logo {
  display: block;
  text-align: center;
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-right: 50px;
  }

  & img {
    @media (min-width: $tablet-width) {
      width: 200px;
      height: 50px;
    }
  }
}

.footer__social {
  @include reset;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 37px;
  order: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-top: -9px;
    justify-content: start;
  }

  @media (min-width: $desktop-width) {
    justify-content: center;
    margin-left: 35px;
  }
}

.footer__copyright {
  display: flex;
  justify-content: space-between;
  padding-top: 17px;
  border-top: 1px solid $background-directions;

  @media (min-width: $tablet-width) {
    width: 22%;
    padding-top: 6px;
  }

  @media (min-width: $desktop-width) {
    justify-content: end;
  }
}

.footer__link {
  line-height: 16px;
  text-decoration: none;

  color: $special-grey;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 25px;
  }
}
